.Header {
  top: 0;
  left: 0;
  position: static;
  z-index: 1100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 40px;
  padding-right: 24px;
  padding-left: 50px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #dfdfdf;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
